@import "../_media";
@import "../_mixins";
@import "../_variables";
.section-teachers {
    margin-top: 98px;
    padding-top: 112px;
    @include mq("tablet") {
        padding-top: 50px;
        margin-top: 58px;
    }
    @include mq("tablet-small") {
        padding-top: 30px;
        margin-top: 38px;
    }
    .col {
        padding-bottom: 49px !important;
        @include mq("tablet") {
            padding-bottom: 10px !important;
        }
    }
    .insta {
        display: inline-flex;
        color: #323031;
        font-size: 14px;
        line-height: 26px;
        font-family: 'SFProDisplay-Semibold', sans-serif;
        transition: all ease-in-out 0.3s;
        margin-bottom: 30px;
        .icon {
            margin-right: 5px;
            &:before {
                font-size: 28px;
                color: #989697;
                transition: all ease-in-out 0.3s;
            }
        }
        a {
            display: inline-flex;
            color: #323031;   
        }
        &:hover {
            .icon {
                &:before {
                    color: #984a8a;   
                }
            }
            a {
                color: #984a8a;
            }
        }

    }
}