@import "../_media";
@import "../_mixins";
@import "../_variables";
.section {
    &-content {
        h2 {
            &:first-child {
                padding-top: 48px;
                margin-bottom: 42px;
                @include mq("tablet-wide") {
                    padding-top: 18px;
                }
                @include mq("tablet") {
                    padding-top: 8px;
                    margin-bottom: 30px;
                }
                @include mq("tablet-small") {
                    margin-bottom: 20px;
                }
            }
        }
        .picture {
            padding-top: 13px;
            margin-bottom: 87px;
            @include mq("tablet-wide") {
                margin-bottom: 57px;
            }
            @include mq("tablet") {
                margin-bottom: 37px;
            }
            img {
                display: block;
                max-width: 100%;
                @include mq("tablet") {
                    object-fit: cover;
                    height: 400px;
                }
                @include mq("tablet-small") {
                    height: 300px;
                }
            }
            & + h2 {
                margin-bottom: 43px;
                @include mq("tablet-small") {
                    margin-bottom: 30px;
                }
            }
        }
        h4 {
            font-size: 24px;
            margin-bottom: 20px;
            color: #000;
            line-height: 31px;
            @include mq("tablet-small") {
                font-size: 22px;
                line-height: 28px;
            }
        }
        p {
            &:last-child {
                margin-bottom: 125px;
                @include mq("tablet-wide") {
                    margin-bottom: 80px;
                }
                @include mq("tablet-small") {
                    margin-bottom: 50px;
                }
            }
            & + h4 {
                padding-top:0;
            }
        }
    }
}