@import "../_media";
@import "../_mixins";
@import "../_variables";
.section-content {
    .content {
        .text-long {
            margin-top: 61px;
            margin-bottom: 67px;
            width: 968px;
            padding-top: 12px;
            border-top: 2px solid #984A8A;
            @include mq("tablet-wide") {
                width: 100% !important;
                padding: 12px 0 0 !important;
            }
            @include mq("tablet") {
                margin-bottom: 50px;
                margin-top: 50px;
            }
            @include mq("tablet-small") {
                margin-bottom: 40px;
                margin-top: 40px;
            }
            p {
                font-size: 22px;
                line-height: 38px;
                margin: 0 0 20px;
                @include mq("tablet") {
                    font-size: 18px;
                    line-height: 32px;
                }
            }
        }
        article {
            margin-bottom: 75px;
            @include mq("tablet") {
                margin-bottom: 50px;
            }
            @include mq("tablet-small") {
                margin-bottom: 30px;
            }
            &:last-child {
                margin-bottom: 96px;
                @include mq("tablet") {
                    margin-bottom: 70px;
                }
                @include mq("tablet-small") {
                    margin-bottom: 50px;
                }
            }
            h2 {
                margin-bottom: 58px;
                @include mq("tablet") {
                    margin-bottom: 40px;
                }
                @include mq("tablet-small") {
                    margin-bottom: 20px;
                }
                &:first-child {
                    padding-top: 0;
                }
            } 
            figure {
                margin-bottom:40px;
                display: flex;
                align-items: flex-end;
                @include mq("tablet") {
                    display: block;
                    margin-bottom: 30px;
                }
                img {
                    display: block;
                    margin-right: 30px;
                    @include mq("tablet-wide") {
                        max-width: 100%;
                        height: auto;
                    }
                    @include mq("tablet") {
                        margin-bottom: 10px;
                    }
                }
                figcaption {
                    font-size: 14px;
                    line-height: 20px;
                    color: #989697;
                    margin-bottom: -5px;
                    max-width: 140px;
                    @include mq("tablet") {
                        max-width: 100%;
                    }
                }
            } 
            p {
                &:last-child {
                    margin-bottom: 26px;
                }
            }  
            p + .row {
                padding-top: 18px;
            } 
            blockquote {
                margin-bottom: 26px;
                p {
                    font-style: italic;
                    margin-bottom: 0;
                    line-height: 26px;
                }
            }
            p + blockquote {
                margin-top: -18px;
            }
        }
    }
}