@import "../_media";
@import "../_mixins";
@import "../_variables";
.section {
    .gallery-works-holder {
        padding-top: 42px;
        padding-bottom: 0;
        @include mq("tablet-small") {
            padding-top: 0;
        }

        .item {
            max-width: 270px;
        }
        .paintings {
            margin-bottom: 35px;
            width: calc(100% + 31px);
            @include mq("tablet-small") {
                width: calc(100% + 15px);
            }
            .item {
                width: calc(100% / 3 - 30px);
                @include mq("tablet-small") {
                    width: calc(100% / 2 - 25px);
                }
            }
        }
    }
    .button-more {
        text-align: center;
        margin-bottom: 60px;
        justify-content: center;
        display: flex;
        @include mq("tablet-small") {
            margin-bottom: 50px;
        }
        a {
            display: flex;
            align-items: center;
            border: 1px solid #989697;
            font-size: 16px;
            line-height: 28px;
            padding: 0 16px;
            width: 270px;
            height: 42px;
            color: #989697;
            position: relative;
            transition: all ease-in-out 0.3s;
            &:hover {
                border: 1px solid #000;
                color: #000;
            }
            &:after {
                content: ' ';
                display: block;
                width: 18px;
                height: 18px;
                position: absolute;
                top: 50%;
                right: 16px;
                transform: translateY(-50%);
                background-image: url(../image/svg/show_small.svg);
            }
            &:hover {
                &:after {
                    background-image: url(../image/svg/show_small-hover.svg);
                }
            }
        }
    }
    .pagination-holder {
        border-top: 2px solid rgba($color: #c6c6c6, $alpha: 0.5);
        padding-top: 38px;
        margin-bottom: 70px;
        display: flex;
        justify-content: space-between; 
        align-items: center; 
        @include mq("tablet-small") {
            justify-content: space-around;
            margin-bottom: 30px;
        }
        .arrow {
            border: solid #984a8a;
            display: inline-block;
            padding: 2px;
            width: 20px;
            height: 20px;
        }
        .prev {
            transform: rotate(135deg);
            border-width: 0 2px 2px 0;
            margin-left: 4px;
            &:hover {
                border: solid #c463b2;
                border-width: 0 2px 2px 0;
            }
        }
        .next {
            margin-right: 4px;
            transform: rotate(-45deg);
            border-width: 0 2px 2px 0;
            &:hover {
                border: solid #c463b2;
                border-width: 0 2px 2px 0;
            }
        }
        .disabled {
            border: solid #ebebeb;
            border-width: 0 2px 2px 0;
            cursor: auto;
            &:hover {
                border: solid #ebebeb;
                border-width: 0 2px 2px 0;
            }
        }
        .pagination {
            display: flex;
            align-items: center;
            margin: 0;
            li {
                padding: 0;
                margin: 0;
                font-size: 16px;
                line-height: 26px;
                margin: 0 18px;
                @include mq("tablet-small") {
                    display: none;
                }
                &:after {
                    display: none !important;
                }
                a {
                    font-size: 16px;
                    line-height: 26px;
                }
                &.active {
                    color: #323031;
                    font-family: 'SFProDisplay-Semibold', sans-serif;
                    @include mq("tablet-small") {
                        display: block;
                    }
                    a {
                        color: #323031;
                    }
                }
            }  
        }        
    }
}
